
body{
  font-family: 'Roboto', sans-serif;
  background-color: var(--white-color);
  color: var(--black-color);
  font-size: 12px;
}


.container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;
  
    @media screen and (min-width:320px){
      width: 320px;
    }
  
    @media screen and (min-width:768px){
      width: 768px;
      padding-left: 32px;
      padding-right: 32px;
    }
    
    @media screen and (min-width:1024px){
      width: 1024px;
      padding-left: 32px;
      padding-right: 32px;
    } 
}

.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
}